/* General Styling */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #2980b9, #6dd5fa, #ffffff);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* App Container */
#root {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px;
}

.app {
  background: rgba(255, 255, 255, 0.9);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 100%;
  max-width: 450px;
  transition: all 0.3s ease;
}

h1 {
  font-size: 2.2em;
  color: #34495e;
  margin-bottom: 10px;
}

p {
  font-size: 1.1em;
  color: #555;
  margin-bottom: 20px;
}

/* Search Bar */
.search-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ecf0f1;
  border-radius: 50px;
  padding: 10px 20px;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.search-bar input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1.2em;
  padding: 5px;
  color: #333;
}

.search-icon {
  font-size: 1.5em;
  color: #3498db;
  cursor: pointer;
  transition: color 0.3s ease;
  margin-left: 10px;
}

.search-icon:hover {
  color: #2c3e50;
}

/* Error Message */
.error {
  color: #e74c3c;
  font-size: 1.2em;
  margin: 15px 0;
  padding: 10px;
  background: rgba(231, 76, 60, 0.1);
  border-radius: 8px;
}

/* Weather Information */
.weather-container {
  margin-top: 30px;
  padding: 20px;
  border-radius: 10px;
  background: #f9f9f9;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.weather-icon {
  font-size: 4em;
  margin-bottom: 15px;
}

.weather-container h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin: 10px 0;
}

.weather-container p {
  font-size: 2.5em;
  font-weight: bold;
  color: #16a085;
  margin: 20px 0;
}

/* Weather Details */
.weather-details {
  display: flex;
  justify-content: space-around;
  padding: 20px 0;
  border-top: 1px solid #eee;
}

.weather-detail {
  text-align: center;
  font-size: 1.1em;
}

.weather-detail span {
  display: block;
  font-weight: bold;
  color: #3498db;
  margin-bottom: 5px;
}

/* Responsive Design */
@media (max-width: 600px) {
  .app {
    width: 90%;
    padding: 20px;
    margin: 10px;
  }

  .search-bar input {
    font-size: 1em;
  }

  .weather-container h2 {
    font-size: 1.5em;
  }

  .weather-container p {
    font-size: 2em;
  }

  .weather-details {
    flex-direction: column;
    gap: 15px;
  }
}
